//This files handles the firebase part of the authentication flow
import { firebaseApp } from "../../firebase/firebase";
import { LoginContext, RegisterContext } from "./ModalContext";

export const registerHandler = async (fields: RegisterContext) => {
  try {
    const isUsernameExists = await !!(
      await firebaseApp
        .firestore()
        .collection("users")
        .where("username", "==", fields.username)
        .get()
    ).docs.length;
    if (isUsernameExists) return [null, "Username is taken"];
    const user = await firebaseApp
      .auth()
      .createUserWithEmailAndPassword(fields.email, fields.password);
    await user.user?.updateProfile({
      displayName: fields.name,
      photoURL: `https://avatars.dicebear.com/api/identicon/${fields.username}.svg`,
    });
    await user.user?.sendEmailVerification();
    const doc = await firebaseApp
      .firestore()
      .collection("users")
      .doc(user?.user?.uid)
      .set({
        username: fields.username,
      })
      .then(async () => {
        const res = await firebaseApp
          .firestore()
          .collection("users")
          .doc(user?.user?.uid)
          .get();
        return res.data();
      });
    const formattedUser = {
      ...user.user?.toJSON(),
      status: "loggedin",
      ...doc,
    };
    return [formattedUser, null];
  } catch (e) {
    return [null, e.message];
  }
};
export const loginHandler = async (fields: LoginContext) => {
  try {
    const user = await firebaseApp
      .auth()
      .signInWithEmailAndPassword(fields.email, fields.password);
    const res = await firebaseApp
      .firestore()
      .collection("users")
      .doc(user?.user?.uid)
      .get();

    const formattedUser = {
      ...user.user?.toJSON(),
      status: "loggedin",
      ...res.data(),
    };

    return [formattedUser, null];
  } catch (e) {
    return [null, e.message];
  }
};
