import { StyleSheet, Platform } from "react-native";

//Auth Styles comes here

const styles = StyleSheet.create({
  container: {
    flex: 1,
    alignItems: "center",
    padding: 50,
  },
  logo: {
    width: 300,
    resizeMode: "contain",
  },
  form: {
    maxWidth: "30%",
    display: "flex",
    alignItems: "center",
  },
  modal: {
    position: "relative",
    flex: 0.5,
    alignItems: "center",
    justifyContent: "center",
    marginTop: "auto",
    backgroundColor: "#fff",
    borderRadius: 20,
  },
  closeModal: {
    position: "absolute",
    left: 10,
    top: 10,
  },
});
const WebStyles = StyleSheet.create({
  ...styles,
  container: {
    alignItems: "center",
  },
  logo: {
    width: "400px",
    height: "200px",
    resizeMode: "contain",
  },
  form: {
    maxWidth: "30%",
    display: "flex",
    alignItems: "center",
  },
  modal: {
    marginBottom: 100,
    position: "absolute",
    bottom: 0,
    backgroundColor: "#fff",
    padding: 20,
    borderRadius: 20,
  },
});

export default Platform.OS === "web" ? WebStyles : styles;
