import { useFormik } from 'formik'
import React, { MutableRefObject, useRef } from 'react'
import { View, TextInputProps, TextInput, StyleProp, ViewStyle } from 'react-native'
import { Input } from './Input'
import { object as Validator } from "yup";
import Button from './Button';
import Tags from 'react-native-tags';
interface TextInputExtended extends TextInputProps {
    //Name should be the same as the state name of input
    name: string,

}
interface FormProps {
    state: any,
    validationSchema?: any,
    containerStyle?: StyleProp<ViewStyle>,
    inputContainerStyle?: StyleProp<ViewStyle>,
    inputStyle?: StyleProp<any>,
    onSubmit: (values: any) => void | Promise<any>;
    children: TextInputExtended[],
    button: {
        title: string,
        style?: any
    },
    Component?: React.ForwardRefExoticComponent<React.RefAttributes<Tags>>
    isComponent?: boolean
}
const Form = (props: FormProps) => {
    const validationSchema = Validator().shape(props.validationSchema)
    const { handleChange, values, handleSubmit, handleBlur,
        errors,
        touched, resetForm } = useFormik({
            initialValues: props.state,
            validationSchema,
            onSubmit: props.onSubmit
        })
    //Mapping all inputs and creating a reference for each one
    const reference = props.children.map(() => useRef<TextInput | null>())
    const componentRef = useRef<Tags>(null);
    //A helper function to reference the next input to focus
    const nextFocus = (index: number) => {
        if (reference.length - 1 === index) handleSubmit();
        else reference[index + 1].current?.focus();
    }
    const _handleSubmit = () => {
        handleSubmit()
        resetForm();
    }
    return (
        <View style={props.containerStyle}>
            {
                props.children.map((InputProps, index) => (
                    <Input validate={!!props.validationSchema} containerStyle={props.inputContainerStyle} style={props.inputStyle}  {...InputProps} value={values[InputProps.name]} onChangeText={handleChange(`${InputProps.name}`)} key={InputProps.name} onBlur={handleBlur(`${InputProps.name}`)} touched={touched[InputProps.name]} errors={errors[InputProps.name]} ref={ref => reference[index].current = ref} onSubmitEditing={() => !errors[InputProps.name] ? nextFocus(index) : null} />
                ))
            }
            {props.Component ?
                <props.Component ref={componentRef} /> : <View></View>}
            <Button style={props.button.style} onPress={() => _handleSubmit()}>{props.button.title}</Button>

        </View>
    )
}

export default Form
