import { registerRootComponent } from 'expo'
import React from 'react'
import { Provider } from 'react-redux'
import App from './App'
import store from './store/store'
//This is the updated entry point file which is overriden by the default App.tsx to ensure redux facilities
const ApplicationRoot = () => {

    return (
        <Provider store={store} >
            <App />
        </Provider >
    )
}

export default registerRootComponent(ApplicationRoot)
