import React, { useEffect, useState } from 'react'
import { Linking, StyleSheet, View } from 'react-native'
import { Icon, Avatar } from 'react-native-elements'
import { useSelector } from 'react-redux'
import Loading from '../../components/Loading'
import { Span, SubHeading } from '../../components/Text'
import { firebaseApp, Firestore } from '../../firebase/firebase'
import { selectUser } from '../../store/taskReducer'
import Frenzy from './FrenzyContext'

const FrenzyComponent = ({ Frenzy, navigation, containerStyle, afterDelete }: any) => {
    const user = useSelector(selectUser)
    const [frenzy, setFrenzy] = useState<Frenzy>(Frenzy)
    useEffect(() => setFrenzy(Frenzy), [Frenzy])
    async function deleteFrenzyBatch(query: any, resolve: any) {
        const snapshot = await query.get();
        const db = firebaseApp.firestore();
        const batchSize = snapshot.size;
        if (batchSize === 0) {
            // When there are no documents left, we are done
            resolve();
            return;
        }

        // Delete documents in a batch
        const batch = db.batch();
        snapshot.docs.forEach((doc: any) => {
            batch.delete(doc.ref);
        });
        await batch.commit();

        // Recurse on the next process tick, to avoid
        // exploding the stack.
        process.nextTick(() => {
            deleteFrenzyBatch(query, resolve);
        });
    }
    const DeleteFrenzy = async (id: string) => {
        try {
            const frenzy = await firebaseApp.firestore().collection("frenzies").doc(id);
            await frenzy.delete();
            const likes = firebaseApp.firestore().collection("frenzies").doc(id).collection("likes");
            const views = firebaseApp.firestore().collection("frenzies").doc(id).collection("views");
            const viewQuery = views.orderBy("createdAt").limit(10);
            const likeQuery = likes.orderBy("createdAt").limit(10);
            new Promise((resolve, reject) => {
                deleteFrenzyBatch(likeQuery, resolve).catch(reject);
            });
            new Promise((resolve, reject) => {
                deleteFrenzyBatch(viewQuery, resolve).catch(reject);
            });
            alert("Deleted the frenzy")
            afterDelete();
        }
        catch {
            alert("Something went wrong..")
        }
    }
    const EditFrenzy = (frenzy: any) => {
        navigation.navigate("AddPost", frenzy)
    }
    const ViewFrenzy = async () => {
        if (!user?.uid) return navigation.navigate("Frenzy", { frenzy: JSON.stringify(frenzy) })
        const base = firebaseApp.firestore().collection("frenzies");
        const path = await base.doc(frenzy.id).collection("views");
        const frenzyAlreadyViewed = await path.doc(user?.uid).get();
        console.log(frenzyAlreadyViewed.exists)
        if (frenzyAlreadyViewed.exists) {
            navigation.navigate("Frenzy", { frenzy: JSON.stringify(frenzy) })
        }
        else {
            await base.doc(frenzy.id).update({
                viewCount: Firestore.FieldValue.increment(1)
            })
            await path.doc(user?.uid).set({
                createdAt: Firestore.FieldValue.serverTimestamp()
            })
            navigation.navigate("Frenzy", { frenzy: JSON.stringify(frenzy) })
        }
    }
    const LikeFrenzy = async () => {
        if (!user?.uid) return;
        const base = firebaseApp.firestore().collection("frenzies");
        const path = await base.doc(frenzy.id).collection("likes");
        const frenzyAlreadyLiked = await path.doc(user?.uid).get();
        if (frenzyAlreadyLiked.exists) {
            await base.doc(frenzy.id).update({
                likeCount: Firestore.FieldValue.increment(-1)
            })
            await path.doc(user?.uid).delete()
        }
        else {
            await base.doc(frenzy.id).update({
                likeCount: Firestore.FieldValue.increment(1)
            })
            await path.doc(user?.uid).set({
                createdAt: Firestore.FieldValue.serverTimestamp(),
            })
        }
    }
    if (!frenzy.id) return <Loading message="Loading frenzy..." />
    return (
        <View style={[styles.frenzyContainer, containerStyle]}>
            <View style={styles.frenzyAvatarContainer}>
                <View style={styles.frenzyAvatar}>
                    <Avatar source={{ uri: frenzy?.photo }} title={frenzy?.user?.charAt(0)} containerStyle={{ backgroundColor: "black", borderRadius: 50 }} titleStyle={{ color: "white" }} />
                    <Span style={{ fontSize: 16, fontWeight: "700", marginLeft: 10 }}>@{frenzy?.user}</Span>
                </View>
                {frenzy?.user === user?.username ? (
                    <View style={styles.frenzyEdit}>
                        <Icon name="edit" type="Ã«ntypo" onPress={() => EditFrenzy(frenzy)} />
                        <Icon name="trash" type="entypo" style={{ marginLeft: 10 }} onPress={() => DeleteFrenzy(frenzy?.id)} />
                    </View>
                ) : <Span></Span>}
            </View>
            <View style={styles.frenzyLink}>
                <SubHeading style={{ color: "blue" }} onPress={() => Linking.openURL(frenzy?.link).catch(e => alert("Couldn't Open the link"))}>{frenzy?.link}</SubHeading>
            </View>
            <View style={styles.frenzyDescription}>
                <Span>{frenzy?.description}</Span>
                {frenzy?.tags?.map((tag: string) => (
                    <Span style={{ color: "blue" }} key={tag}>#{tag}</Span>
                ))}
            </View>
            <View style={styles.frenzyIcons}>
                <View style={styles.frenzyIcon}>
                    <Icon name="eye" type="antdesign" onPress={() => ViewFrenzy()} />
                    <Span>{frenzy?.viewCount}</Span>
                </View>
                <View style={styles.frenzyIcon}>
                    <Icon name={frenzy?.liked ? "heart" : "hearto"} type="antdesign" onPress={() => LikeFrenzy()} />
                    <Span>{frenzy?.likeCount}</Span>
                </View>
                <View style={styles.frenzyIcon}>
                    <Icon name="chat" type="entypo" onPress={() => ViewFrenzy()} />
                    <Span>{frenzy?.commentCount}</Span>
                </View>

            </View>
        </View>
    )
}

export default FrenzyComponent

const styles = StyleSheet.create({
    frenzyContainer: {
        backgroundColor: "white",
        marginLeft: "auto",
        marginRight: "auto",
        marginVertical: 40,
        width: "60%",
        minWidth: 340,
        borderRadius: 20
    },
    frenzyAvatarContainer: {
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "space-between",
        padding: 10,
        borderBottomColor: "gray",
        borderBottomWidth: 2
    },
    frenzyAvatar: {
        flexDirection: "row",
        alignItems: "center"
    },
    frenzyEdit: {
        flexDirection: "row"
    },
    frenzyLink: {
        padding: 10
    },
    frenzyDescription: {
        padding: 10,
        backgroundColor: "#ebebeb",
        margin: 10,
        borderRadius: 10
    },
    frenzyIcons: {
        display: "flex",
        flexDirection: "row",
        justifyContent: "space-evenly",
        padding: 10
    },
    frenzyIcon: {
        display: "flex",
        alignItems: "center"
    }
})
