import React from 'react'
import { View } from 'react-native'
import { Icon } from 'react-native-elements'
import { useSelector } from 'react-redux'
import { Span, SubHeading } from '../../../components/Text'
import { selectUser } from '../../../store/taskReducer'
import { reply } from '../FrenzyContext'

const Reply = (props: { reply: reply }) => {
    const { reply } = props;
    const user = useSelector(selectUser);
    return (
        <View style={{ flexDirection: "row", marginLeft: 20, borderTopColor: "gray", borderBottomColor: "gray", borderTopWidth: 2, borderBottomWidth: 2, justifyContent: "space-between", alignItems: "center", marginVertical: 10, padding: 10 }}>
            <View>
                <Span style={{ fontWeight: "700", color: "orange" }}>@{reply.by}{" "}<Span style={{ opacity: 0.8, fontStyle: "italic" }}>said</Span></Span>
                <SubHeading style={{ fontSize: 18, marginLeft: 2, marginVertical: 5 }}>{reply.reply}</SubHeading>
            </View>
            {reply.by === user?.username ?
                <View style={{ flexDirection: "row", width: 100, justifyContent: "space-between" }}>
                    <Icon name="edit" type="entypo" />
                    <Icon name="trash" type="entypo" />
                </View>
                : null}
        </View>
    )
}

export default Reply
