import React, { useEffect, useState } from 'react'
//Components
import { Span } from '../../components/Text'
import Loading from '../../components/Loading'
import { Dimensions, View } from 'react-native'
import Button from '../../components/Button'
//Redux
import { useSelector } from 'react-redux'
import { selectUser } from '../../store/taskReducer'
//Firebase
import { firebaseApp } from '../../firebase/firebase'
//Context
import Frenzy from '../Frenzy/FrenzyContext'
const FrenzyQuery = ({ children }: any) => {
    //Initial State
    const [frenzies, setfrenzies] = useState<Frenzy[] | null | "loading">(null)
    //Pagination page
    const [page, setPage] = useState(0);
    const pageSize = 2;
    //Query
    const basePath = firebaseApp.firestore().collection("frenzies");
    //Fetch 1 more to determine if there are more coming in next page
    const [query, setQuery] = useState(basePath.orderBy("createdAt", "desc").limit(pageSize + 1))
    //Current User
    const user = useSelector(selectUser);
    const nextPage = (last: Frenzy) => {
        setPage(page + 1)
        setQuery(query
            .startAt(last["createdAt"])
            .limit(pageSize + 1));
    }
    const prevPage = (first: Frenzy) => {
        console.log("Previous Change" + JSON.stringify(first) + "\n\n")
        setPage(page - 1)
        query
            .endBefore(first.createdAt)
            .limit(3).get().then(res => console.log(res.docs.map(r => r.data())))
        setQuery(query
            .endBefore(first["createdAt"])
            .limitToLast(pageSize))
    }
    useEffect(() => {
        const unsubscribe = query.onSnapshot((_frenzies) => {
            setfrenzies("loading")
            console.log("Change!!")
            const promise = _frenzies.docs.map(async (doc) => {
                return {
                    ...doc.data(),
                    liked: await (await doc.ref.collection("likes").doc(user?.uid).get()).exists,
                    viewed: await (await doc.ref.collection("views").doc(user?.uid).get()).exists,
                    id: doc.id
                }
            })
            Promise.all(promise).then((result: any) => {
                setfrenzies(result)
            })
        })
        return () => unsubscribe();
    }, [query, page])

    if (!frenzies || frenzies === "loading") return (
        <View style={{ minHeight: Dimensions.get("window").height }}>
            <Loading message="Loading Frenzies.." />
        </View>
    )
    console.log(frenzies)
    return (
        <View>
            {frenzies.length > pageSize ? children({ frenzies: frenzies.filter((_, index) => index < pageSize) }) : children({ frenzies })}
            <View>
                <Button onPress={() => nextPage(frenzies[frenzies?.length - 1])} disabled={frenzies.length < pageSize} style={[{ marginLeft: "auto", marginRight: "auto", width: 300 }, frenzies.length < pageSize ? { opacity: 0.5 } : {}]}>
                    Next ð
                </Button>
                <Button onPress={() => prevPage(frenzies[0])} disabled={page === 0} style={[{ marginLeft: "auto", marginRight: "auto", width: 300 }, page === 0 ? { opacity: 0.5 } : {}]}>
                    Previous ð
                </Button>
                <Span style={{ textAlign: "center", marginTop: 20 }}>Page : {page + 1}</Span>
            </View>
        </View>
    )

}

export default FrenzyQuery
