import React, { useEffect, useState } from 'react'
import { View, Dimensions } from 'react-native'
import { Icon } from 'react-native-elements';
import { useSelector } from 'react-redux';
import Form from '../../../components/Form';
import { Span, SubHeading } from '../../../components/Text';
import { firebaseApp, Firestore } from '../../../firebase/firebase';
import { selectUser } from '../../../store/taskReducer';
import { comment } from '../FrenzyContext';
import Reply from './Reply';

const Comment = (props: { Comment: comment, frenzyId: string }) => {
    const { Comment, frenzyId } = props;
    const [comment, setComment] = useState<comment>({ ...Comment, replies: [] })
    const [seeReply, setSeeReply] = useState(false)
    const user = useSelector(selectUser)
    const addReply = async (reply: string) => {
        try {

            await firebaseApp.firestore().collection("frenzies").doc(frenzyId).collection("comments").doc(comment.id).collection("replies").add({
                createdAt: Firestore.FieldValue.serverTimestamp(),
                updatedAt: Firestore.FieldValue.serverTimestamp(),
                reply,
                by: user?.username
            })
        }
        catch (e) {
            alert("Could not add your reply")
        }
    }
    useEffect(() => {
        const unsubscribe = firebaseApp.firestore().collection("frenzies").doc(frenzyId).collection("comments").doc(comment.id).collection("replies").onSnapshot(replies => {
            const data: any = [];
            replies.docs.map(reply => {
                data.push({ ...reply.data(), id: reply.id })
            })
            setComment({ ...Comment, replies: data })
        })
        return () => unsubscribe();
    }, [Comment])
    return (
        <View style={{ backgroundColor: "white", paddingHorizontal: 20, paddingVertical: 10, marginVertical: 20, width: "80%", minWidth: Dimensions.get("window").width }}>
            <View style={{ flexDirection: "row", justifyContent: "space-between", alignItems: "center" }}>
                <View>
                    <Span style={{ fontWeight: "700", color: "orange" }}>@{comment.by}{" "}<Span style={{ opacity: 0.8, fontStyle: "italic" }}>said</Span></Span>
                    <SubHeading style={{ fontSize: 18, marginLeft: 2, marginVertical: 5 }}>{comment.comment}</SubHeading>
                    <Span onPress={() => setSeeReply(!seeReply)} style={{ textDecoration: "underline", color: "blue" }}>View Reply</Span>
                </View>
                {comment.by === user?.username ?
                    <View style={{ flexDirection: "row", width: 100, justifyContent: "space-between" }}>
                        <Icon name="edit" type="entypo" />
                        <Icon name="trash" type="entypo" />
                    </View>
                    : null}
            </View>
            {seeReply ?
                <>
                    {user?.uid ? (
                        <View style={{ alignSelf: "stretch" }}>
                            <Form inputContainerStyle={{ flex: 1, alignItems: "center", }} inputStyle={{ alignSelf: "stretch", width: "auto", borderRadius: 0, borderColor: "gray", borderWidth: 1 }} containerStyle={{ flexDirection: "row", alignItems: "flex-end" }} state={{ reply: "" }} children={[
                                {
                                    name: "reply",
                                    placeholder: `Replying to ${comment.by}`
                                }
                            ]} button={{ title: "Reply", style: { marginTop: 0, borderRadius: 0, paddingVertical: 15, width: "auto" } }} onSubmit={(values) => addReply(values?.reply)} isComponent={false} />
                        </View>
                    ) : null}
                    <View>
                        {
                            comment.replies.map(reply => (
                                <Reply key={reply.id} reply={reply} />
                            ))
                        }
                    </View>
                </>
                : null}
        </View>
    )
}

export default Comment
