import React, { useState } from 'react'
//Navigation
import { StackNavigationProp } from '@react-navigation/stack'
import { ParamListBase, useNavigation } from '@react-navigation/native';
//Components
import { SafeAreaView, Image, KeyboardAvoidingView, ScrollView, View } from 'react-native'
import Loading from '../../components/Loading';
import Button from '../../components/Button'
import { Span } from '../../components/Text';
//Styles and helper files
import styles from "./styles";
import SocialLogins from './SocialLogins'
import { RegisterContext } from './ModalContext';
import { registerHandler } from './handler';
//Form
import Form from '../../components/Form'
import * as Yup from "yup";
//Redux
import { useDispatch, useSelector } from 'react-redux';
import { selectLoading, setUser, setUserErrors, setUserLoading, userError } from '../../store/taskReducer';
const Register = () => {
    const navigation = useNavigation<StackNavigationProp<ParamListBase, "Register">>()
    const error = useSelector(userError)
    const dispatch = useDispatch();
    const loading = useSelector(selectLoading)
    const register = async (values: RegisterContext) => {
        //Set loading to true
        dispatch(setUserLoading(true))
        const [user, error] = await registerHandler(values);
        if (user) {
            //navigate the user
            alert('Account has been created and a confirmation link has been sent to your email. Confirm your email and login')
            navigation.navigate("Login")
        }
        if (error) dispatch(setUserErrors(error))
        //Set loading to false
        dispatch(setUserLoading(false))
    }
    if (loading.load) return <Loading />
    else return (
        <ScrollView>
            <SafeAreaView style={styles.container}>
                <Image source={require("../../assets/logo.png")} style={styles.logo} />
                <KeyboardAvoidingView>
                    <Span>{error}</Span>
                    <Form state={{ name: '', username: '', email: '', password: '' }} validationSchema={{
                        name: Yup.string().required("Required"),
                        username: Yup.string().required("Required"),
                        email: Yup.string().email('Invalid email').required('Required'),
                        password: Yup.string()
                            .min(6, 'Too Short!')
                            .required('Required')
                    }} children={
                        [
                            {
                                placeholder: "Name",
                                textContentType: "name",
                                name: "name"
                            },
                            {
                                placeholder: "Username",
                                textContentType: "username",
                                name: "username"
                            },
                            {
                                placeholder: "Email",
                                keyboardType: "email-address",
                                textContentType: "emailAddress",
                                name: "email"
                            },
                            {
                                placeholder: "Password",
                                secureTextEntry: true,
                                textContentType: "password",
                                name: "password"
                            },
                        ]
                    }
                        button={{ title: "Register" }}
                        onSubmit={(values: RegisterContext) => register(values)}
                    />
                    <Button onPress={() => navigation.replace("Login")} bg="white" textColor="black">Login</Button>
                </KeyboardAvoidingView>
                <SocialLogins />
            </SafeAreaView>
            {/* // Ensure the components do not touch the bottom with a margin  */}
            <View style={{ height: 50 }} />
        </ScrollView>
    )
}

export default Register

