import React, { useEffect, useState } from 'react';
import { Platform, LogBox, View } from "react-native"
//Redux
import { useDispatch, useSelector } from "react-redux";
import { selectUser, setFontsLoading, setUserLoading, setUser } from "./store/taskReducer";
//Navigation
import "react-native-gesture-handler";
import { NavigationContainer } from '@react-navigation/native';
import { createStackNavigator } from '@react-navigation/stack';
//Screens
import Login from './screens/Auth/Login';
import Register from './screens/Auth/Register';
import Home from './screens/Home/Home';
import AddPost from './screens/Home/AddPost';
import Loading from './components/Loading';
import Error from './components/Error';
import Frenzy from './screens/Frenzy/Frenzy';
//Fonts
import {
  useFonts,
  BalooChettan2_400Regular as ViralFont
} from '@expo-google-fonts/baloo-chettan-2'
//Firebase
import { firebaseApp } from './firebase/firebase';

export default function App() {
  const Stack = createStackNavigator();
  //Surpressing unneccessary warning
  if (Platform.OS !== "web") LogBox.ignoreLogs(['Setting a timer for a long period of time']);
  //Check if app is loading and if there is any async function going on
  //Get the user
  const user = useSelector(selectUser)
  const [_userLoading, _setUserLoading] = useState(false);
  const [userError, setUserError] = useState("");
  const dispatch = useDispatch();
  let [fontsLoaded] = useFonts({
    ViralFont
  });
  // //FontsLoaded is a boolean which does what it means.so whenever it changes loading state changes opposite to the variable like if fontsloaded===true then loading=false
  useEffect(() => {
    if (fontsLoaded) dispatch(setFontsLoading(false))
    else dispatch(setFontsLoading(true))
  }, [fontsLoaded])
  useEffect(() => {
    dispatch(setUserLoading(_userLoading))
  }, [_userLoading])
  useEffect(() => {
    if (!user?.uid && user?.status !== "signedout") {
      _setUserLoading(true)
      const unsubscribe = firebaseApp.auth().onAuthStateChanged(user => {
        firebaseApp.firestore().collection("users").doc(user?.uid).get().then(data => {
          dispatch(setUser({ ...user?.toJSON(), ...data.data(), status: "loggedin" }))
          _setUserLoading(false)
        }).catch(e => {
          if (e.code === "unavailable") setUserError("Check Your Network Connection And Try Again")
          else setUserError(e.message)
          _setUserLoading(false)
        })
      })
      return () => unsubscribe();
    }
  }, [])

  if (userError) return <Error message={userError} />
  if (!fontsLoaded || _userLoading) return <Loading />
  else return (
    <>
      <NavigationContainer>
        {/* Global Options for Navigator */}
        <Stack.Navigator initialRouteName="Home" screenOptions={{ headerTitleAlign: "center", headerTitleStyle: { fontFamily: "ViralFont", fontWeight: "800" } }}>
          <Stack.Screen name="Home" component={Home} />
          <Stack.Screen name="AddPost" component={AddPost} />
          <Stack.Screen name="Frenzy" component={Frenzy} />
          <Stack.Screen name="Login" component={Login} />
          <Stack.Screen name="Register" component={Register} />
        </Stack.Navigator>
      </NavigationContainer>
    </>
  );
}
