import { createSlice } from "@reduxjs/toolkit";
import { StateContext } from "./taskContext";

const initialState: StateContext = {
  user: null,
  //errors
  userErrors: null,
  //Loaders
  userLoading: null,
  fontsLoading: null,
};
const taskReducer = createSlice({
  name: "reducer",
  initialState,
  reducers: {
    setUser: (state, action) => {
      state.user = action.payload;
    },
    setUserErrors: (state, action) => {
      state.userErrors = action.payload;
    },
    setUserLoading: (state, action) => {
      state.userLoading = action.payload;
    },
    setFontsLoading: (state, action) => {
      state.fontsLoading = action.payload;
    },
  },
});

export const { setUser, setUserErrors, setFontsLoading, setUserLoading } =
  taskReducer.actions;

//Selectors
export const selectUser = (state: { reducer: StateContext }) =>
  state.reducer.user;
export const userError = (state: { reducer: StateContext }) =>
  state.reducer.userErrors;
export const selectLoading = (state: { reducer: StateContext }) => {
  const { fontsLoading, userLoading } = state.reducer;
  const _fontsLoading = fontsLoading || fontsLoading === null;
  return {
    load: userLoading || _fontsLoading,
    which: _fontsLoading ? "Assets" : userLoading ? "Session" : "Viral Page",
  };
};
export default taskReducer.reducer;
