import React from "react"
import { Text } from "react-native"

export const Heading = ({ children, onPress }: any) => {
    return <Text onPress={onPress} style={{ color: "#000", fontSize: 48, fontFamily: "ViralFont" }}>{children}</Text>
}
export const SubHeading = ({ children, style, onPress }: any) => {
    return <Text onPress={onPress} style={{ color: "#000", fontSize: 24, fontFamily: "ViralFont", ...style }}>{children}</Text>
}
export const Span = ({ children, style, onPress }: any) => {
    return <Text onPress={onPress} style={{ fontSize: 14, fontFamily: "ViralFont", marginVertical: 0, ...style }}>{children}</Text>
}
export const Paragraph = ({ children, onPress }: any) => {
    return <Text onPress={onPress} style={{ color: "#000", fontSize: 16, fontFamily: "ViralFont", lineHeight: 20, letterSpacing: 10 }}>{children}</Text>
}