import React from 'react'
//Navigation
import { StackNavigationProp } from '@react-navigation/stack'
import { ParamListBase, useNavigation } from '@react-navigation/native';
//Components
import { SafeAreaView, Image, KeyboardAvoidingView, ScrollView, View } from 'react-native'
import Button from '../../components/Button'
import { Span } from '../../components/Text';
import Loading from '../../components/Loading';
//Styles and helper files
import styles from "./styles";
import SocialLogins from './SocialLogins'
import { loginHandler } from './handler';
import { LoginContext } from './ModalContext';
//Form
import Form from '../../components/Form'
import * as Yup from "yup";
//Redux
import { useDispatch, useSelector } from 'react-redux';
import { selectLoading, setUser, setUserErrors, setUserLoading, userError } from '../../store/taskReducer';
const Login = () => {
    const navigation = useNavigation<StackNavigationProp<ParamListBase, "Login">>()
    const error = useSelector(userError);
    const dispatch = useDispatch();
    const loading = useSelector(selectLoading)
    const login = async (values: LoginContext) => {
        //Set loading to true
        dispatch(setUserLoading(true))
        const [user, error] = await loginHandler(values);
        
        if (error) {
            dispatch(setUserErrors(error))
            dispatch(setUserLoading(false))
        }
        else if (user) {
            if (user?.emailVerified) {
                
                dispatch(setUser(user))
                
                //navigate the user
                navigation.replace("Home")
                dispatch(setUserLoading(false))
            }
            else if (!user?.emailVerified) {
                dispatch(setUserErrors("Account is not verified"))
                dispatch(setUserLoading(false))
            }
        }
    }
    if (loading.load) return <Loading />
    else return (
        <ScrollView>
            <SafeAreaView style={styles.container}>
                <Image source={require("../../assets/logo.png")} style={styles.logo} />
                <KeyboardAvoidingView style={styles.form}>
                    <Span style={{ width: 320, textAlign: "center" }}>{error}</Span>
                    <Form state={{ email: '', password: '' }}
                        children={[
                            {
                                placeholder: "Email",
                                keyboardType: "email-address",
                                textContentType: "emailAddress",
                                name: "email",
                            },
                            {
                                placeholder: "Password",
                                secureTextEntry: true,
                                textContentType: "password",
                                name: "password",
                            },
                        ]}
                        validationSchema={{
                            email: Yup.string().email('Invalid email').required('Required'),
                            password: Yup.string().required('Required')
                        }}
                        button={{ title: "Login" }}
                        onSubmit={(values: LoginContext) => login(values)}
                    />
                    <Button onPress={() => navigation.replace("Register")} bg="white" textColor="black">Register</Button>
                </KeyboardAvoidingView>
                <SocialLogins />
            </SafeAreaView>
            {/* // Ensure the components do not touch the bottom with a margin  */}
            <View style={{ height: 50 }} />
        </ScrollView>
    )
}

export default Login

