import React, { Component, useState } from 'react'
//For web
import { Modal as WebModal } from 'react-native-paper'
//For mobile
import { Modal as MobileModal } from 'react-native'
import { Platform, View } from 'react-native'
import { Icon } from 'react-native-elements'
import Button from '../../components/Button'
import styles from './styles'

const SocialLogins = () => {
    const [socialToggle, setSocialToggle] = useState(false)
    return (
        <View>
            <Button onPress={() => setSocialToggle(true)} textColor="black" bg="white">Social Logins</Button>
            {Platform.OS === "web" ? (
                <WebModal visible={socialToggle} contentContainerStyle={styles.modal} overlayAccessibilityLabel="Close">
                    <Icon name="close" containerStyle={styles.closeModal} type="antdesign" onPress={() => setSocialToggle(false)} />
                    {/* Buttons for OAUTH Authentication */}
                    <Button bg="#de5246" mt={20}>Google</Button>
                    <Button bg="#4267B2" mt={20}>Facebook</Button>
                    <Button bg="#1DA1F2" mt={20}>Twitter</Button>

                </WebModal>
            ) : (
                <MobileModal animationType="slide" transparent={true} visible={socialToggle} >
                    <View style={styles.modal}>
                        <Icon name="close" containerStyle={styles.closeModal} type="antdesign" onPress={() => setSocialToggle(false)} />
                        {/* Buttons for OAUTH Authentication */}
                        <Button bg="#de5246" mt={20}>Google</Button>
                        <Button bg="#4267B2" mt={20}>Facebook</Button>
                        <Button bg="#1DA1F2" mt={20}>Twitter</Button>

                    </View>
                </MobileModal>
            )}
        </View>
    )

}

export default SocialLogins
