import firebase from "firebase";
//Services
import "firebase/app";
import "firebase/auth";
import "firebase/firestore";
//Firebase Setup
//TO change the api keys refer  to .env file in root directory
//Environments
import {
  API_KEY,
  AUTHDOMAIN,
  DB_URL,
  PROJ_ID,
  STORAGEBUCKET,
  MSID,
  APPID,
} from "@env";
const firebaseConfig = {
  apiKey: API_KEY,
  authDomain: AUTHDOMAIN,
  databaseURL: DB_URL,
  projectId: PROJ_ID,
  storageBucket: STORAGEBUCKET,
  messagingSenderId: MSID,
  appId: APPID,
};
const firebaseApp = firebase.apps.length
  ? firebase.app()
  : firebase.initializeApp(firebaseConfig);
const Auth = firebase.auth;
const Firestore = firebase.firestore;
export { firebaseApp, Auth, Firestore };
export interface UserContext extends firebase.User {
  username: string;
  status: "signedout" | "loggedin" | "idle";
}
