import React from 'react'
import { View, Text } from 'react-native'
//Redux
import { useSelector } from 'react-redux'
import { selectLoading } from '../store/taskReducer'
interface LoadingProps {
    message?: string,
}
const Loading = (props: LoadingProps) => {
    const loading = useSelector(selectLoading)
    return (
        <View style={{ flex: 1, alignItems: "center", justifyContent: "center", backgroundColor: "#ebeced" }}>
            <Text style={{ fontFamily: "monospace", fontSize: 30 }}>{props.message || `Loading ${loading.which}...`}</Text>
        </View>
    )
}

export default Loading
