import React from 'react'
import { View, Text, Image, Platform, NativeModules } from 'react-native'
import Button from './Button'
interface ErrorProps {
    message?: string,
}
const Error = (props: ErrorProps) => {
    return (
        <View style={{ flex: 1, alignItems: "center", justifyContent: "center", backgroundColor: "#ebeced" }}>
            <Image source={require("../assets/logo.png")} style={{ width: 400, height: 200, resizeMode: "contain" }} />
            <Text style={{ fontFamily: "monospace", fontSize: 30 }}>{props.message || "Error..."}</Text>
            <Button onPress={() => NativeModules.DevSettings.reload()}>Retry</Button>
        </View>
    )
}

export default Error
