import React, { useLayoutEffect } from 'react'
import { Image, StyleSheet, View, SafeAreaView, StatusBar, TouchableOpacity, ScrollView, RefreshControl, NativeModules } from 'react-native'
//Navigation
import { useNavigation } from '@react-navigation/core'
import { ParamListBase } from '@react-navigation/native'
import { StackNavigationProp } from '@react-navigation/stack'
//Components
import { Span, SubHeading } from '../../components/Text'
import FrenzyComponent from '../Frenzy/FrenzyComponent'
import Frenzy from '../Frenzy/FrenzyContext'
import FrenzyQuery from './FrenzyQuery'
//Redux 
import { useDispatch, useSelector } from 'react-redux'
import { selectUser, setUser } from '../../store/taskReducer'
//Firebase
import { firebaseApp } from '../../firebase/firebase'

const Home = () => {
    //Refresh state
    const [refreshing, setRefreshing] = React.useState(false);
    //Refresh helper
    const onRefresh = React.useCallback(() => {
        setRefreshing(true);
        NativeModules.DevSettings.reload()
    }, []);
    //Navigation Init
    const navigation = useNavigation<StackNavigationProp<ParamListBase, 'Home'>>();
    //User
    const user = useSelector(selectUser)
    //helper function
    const logout = () => {
        firebaseApp.auth().signOut().then(() => {
            dispatch(setUser({ status: "signedout" }));
            alert("Successfully Logged Out")
            navigation.replace("Home")
        })
    }
    const handleHeaderBtnPress = () => {
        if (user?.uid) logout()
        else navigation.navigate("Login")
    }

    //Styling Navigation Header
    useLayoutEffect(() => {
        navigation.setOptions({
            header: () => (
                <SafeAreaView style={styles.header}>
                    <Image source={require("../../assets/icon.png")} style={styles.headerImage} />
                    <TouchableOpacity onPress={handleHeaderBtnPress}>
                        <SubHeading>{user?.uid ? "Sign Out" : "Sign In"}</SubHeading>
                    </TouchableOpacity>
                </SafeAreaView>
            ),
        })
    }, [navigation])
    const dispatch = useDispatch();

    return (
        <ScrollView bounces={true} refreshControl={
            <RefreshControl
                refreshing={refreshing}
                onRefresh={onRefresh}

            />}>
            {user?.uid ?
                <TouchableOpacity onPress={() => navigation.navigate("AddPost")} style={styles.newPost}>
                    <Span style={{ color: "white", textAlign: "center" }}>New Post</Span>
                </TouchableOpacity>
                : <SubHeading></SubHeading>}
            <FrenzyQuery>
                {({ frenzies }: { frenzies: Frenzy[] }) => {
                    return (
                        <>
                            {frenzies.map(frenzy => (
                                <FrenzyComponent Frenzy={frenzy} key={frenzy.id} navigation={navigation} />
                            ))}
                        </>
                    )
                }}
            </FrenzyQuery>
            <View style={{ height: 50 }} />

        </ScrollView>
    )
}

export default Home

const styles = StyleSheet.create({
    header: {
        backgroundColor: "white",
        display: "flex",
        flexDirection: "row",
        alignItems: "center",
        justifyContent: "space-between",
        paddingTop: StatusBar.currentHeight,
        paddingRight: 20,
        paddingLeft: 20,
    },
    headerImage: {
        width: 48,
        height: 48
    },
    headerBtn: {
        width: 100,
        marginTop: 0,
        padding: 10
    },
    newPost: {
        padding: 10,
        backgroundColor: "black"
    },

})
