import React, { useLayoutEffect, useState } from 'react'
import { View, Text, TouchableOpacity, ScrollView } from 'react-native'
//Navigation
import { useNavigation } from '@react-navigation/core'
import { ParamListBase } from '@react-navigation/native'
import { StackNavigationProp } from '@react-navigation/stack'
//Components
import Form from '../../components/Form'
import Loading from '../../components/Loading'
import Tags from "react-native-tags";
import { Heading, SubHeading } from '../../components/Text'
import { Icon } from 'react-native-elements'
import Button from '../../components/Button'
import * as Yup from "yup"
import { Modal } from 'react-native-paper'
//Firestore
import { firebaseApp, Firestore } from '../../firebase/firebase'
//Redux
import { useSelector } from "react-redux";
import { selectUser } from "../../store/taskReducer"
//Context
import Frenzy from '../Frenzy/FrenzyContext'
const AddPost = ({ route }: any) => {
    //Navigation Init
    const navigation = useNavigation<StackNavigationProp<ParamListBase, 'AddPost'>>();
    useLayoutEffect(() => {
        navigation.setOptions({
            headerTitle: "Create a new frenzy ð¥"
        })
    }, [])
    const user = useSelector(selectUser)
    const launchFrenzy = (values: any) => {
        setLoading(true);
        if (route.params?.id) {
            firebaseApp.firestore().collection("frenzies").doc(route.params.id).update({
                ...values,
                updatedAt: Firestore.FieldValue.serverTimestamp()
            }).then(() => {
                firebaseApp.firestore().collection("frenzies").doc(route.params.id).get().then(res => {
                    setLoading(false)
                    setNewFrenzy(res.data() as Frenzy)
                })
            }).catch(e => {
                setError(e.message)
            })
        }
        else {
            firebaseApp.firestore().collection("frenzies").add({
                ...values,
                user: user?.username,
                photo: user?.photoURL,
                likeCount: 0,
                viewCount: 0,
                commentCount: 0,
                createdAt: Firestore.FieldValue.serverTimestamp(),
                updatedAt: Firestore.FieldValue.serverTimestamp()
            }).then((newFrenzy) => {
                newFrenzy.get().then(res => {
                    setLoading(false)
                    setNewFrenzy(res.data() as Frenzy)
                })
            }).catch(e => {
                setError(e.message)
            })
        }
    }
    const [loading, setLoading] = useState(false);
    const [error, setError] = useState<null | string>(null);
    const [newFrenzy, setNewFrenzy] = useState<null | Frenzy>(null);
    const [descriptionInputHeight, setDIH] = useState<number | null>(null)
    const [tags, setTags] = useState<string[]>(route.params?.tags || [])
    if (loading) return <Loading message="Adding Frenzy..." />
    return (
        <ScrollView style={{ display: "flex" }} contentContainerStyle={{ alignItems: "center", justifyContent: "center", minHeight: "100%" }}>

            <Form
                validationSchema={{
                    link: Yup.string().url("Invalid URL").required("Required!"),
                }}
                state={{ link: route.params?.link || "", description: route.params?.description || "" }}
                children={[
                    {
                        name: "link",
                        placeholder: "Link",
                        textContentType: "URL"
                    },
                    {
                        name: "description",
                        placeholder: "Description",
                        multiline: true,
                        style: { height: descriptionInputHeight || "auto" },
                        onContentSizeChange: (e) => setDIH(e.nativeEvent.contentSize.height)
                    },
                ]}
                Component={React.forwardRef((_, ref) => {
                    return (
                        <Tags
                            initialText=""
                            textInputProps={{
                                placeholder: "Tags"
                            }}
                            initialTags={tags}
                            onChangeTags={tags => setTags(tags)}
                            onTagPress={(index, tagLabel, event, deleted) =>
                                console.log(index, tagLabel, event, deleted ? "deleted" : "not deleted")
                            }

                            containerStyle={{ width: 300 }}
                            inputContainerStyle={{
                                backgroundColor: "none",
                                width: 300,
                                flexBasis: "100%",
                                height: 60,
                                marginBottom: 20,
                                marginTop: 20
                            }}
                            inputStyle={{
                                backgroundColor: "#fff",
                                paddingHorizontal: 20,
                                fontFamily: "ViralFont",
                                fontSize: 18,
                                borderRadius: 10,
                            }}
                            maxNumberOfTags={30}

                            renderTag={({ tag, index, onPress }) => (
                                <TouchableOpacity key={`${tag}-${index}`} onPress={onPress} style={{
                                    backgroundColor: "black",
                                    minWidth: 50,
                                    marginVertical: 10,
                                    borderRadius: 20,
                                    paddingVertical: 10,
                                    paddingHorizontal: 15,
                                    marginTop: 10,
                                    marginHorizontal: 5
                                }} >
                                    <Text style={{ color: "white", textAlign: "center", fontWeight: "700" }}>#{tag}</Text>
                                </TouchableOpacity>
                            )
                            }


                        />

                    )
                })}
                isComponent={true}
                button={
                    {
                        title: "Launch Frenzy ð",
                    }
                }
                onSubmit={(values) => launchFrenzy({ ...values, tags })}
            />
            <Text style={{ fontStyle: "italic", textAlign: "center", marginTop: 20 }}>Click space to add a tag after typing and press the tag to remove it</Text>
            {/* Success Message */}
            <Modal visible={!!newFrenzy} dismissable contentContainerStyle={{ backgroundColor: "white", width: 320, marginLeft: "auto", marginRight: "auto", display: "flex", alignItems: "center", paddingVertical: 20, borderRadius: 20 }}>
                <Icon name="close" containerStyle={{ alignSelf: "flex-start", marginLeft: 10 }} type="antdesign"
                    onPress={() => {
                        setNewFrenzy(null);
                        navigation.goBack()
                    }
                    } />
                <Heading>ð</Heading>
                <SubHeading>Frenzy is Launched</SubHeading>
                <Button onPress={() => navigation.navigate("Frenzy", { frenzy: JSON.stringify(newFrenzy) })}>View</Button>
            </Modal>
            {/* Error Message */}
            <Modal visible={!!error} dismissable contentContainerStyle={{ backgroundColor: "white", width: 320, marginLeft: "auto", marginRight: "auto", display: "flex", alignItems: "center", paddingVertical: 20, borderRadius: 20 }}>
                <Icon name="close" containerStyle={{ alignSelf: "flex-start", marginLeft: 10 }} type="antdesign"
                    onPress={() => {
                        setError(null);
                    }
                    } />
                <Heading>ð­</Heading>
                <SubHeading>{error || "Something went wrong"}</SubHeading>
                <Button onPress={() => {
                    setError(null);
                }}>Retry</Button>
            </Modal>
        </ScrollView >
    )
}

export default AddPost
