import { FormikErrors, FormikTouched } from "formik"
import React, { LegacyRef } from "react"
import { TextInput, TextInputProps, StyleSheet, View, StyleProp, ViewStyle } from "react-native";
import { Span } from "./Text";
interface TextInputExtended extends TextInputProps {
    containerStyle?: StyleProp<ViewStyle>;
    validate: boolean;
    touched: boolean | FormikTouched<any> | FormikTouched<any>[] | undefined;
    errors: string | FormikErrors<any> | string[] | FormikErrors<any>[] | undefined;
}
export const Input = React.forwardRef((props: TextInputExtended, ref: LegacyRef<TextInput>) => {
    const validationColor = !props.touched ? '#ff4d00' : props.errors ? '#FF5A5F' : '#ff4d00';
    const styles = StyleSheet.create({
        input: {
            backgroundColor: "#fff",
            shadowColor: validationColor,
            marginTop: 20,
            width: 300,
            paddingHorizontal: 20,
            paddingVertical: 14,
            fontFamily: "ViralFont",
            fontSize: 18,
            borderRadius: 10,

        }
    })
    return (
        <View style={props.containerStyle}>
            <TextInput {...props} style={[styles.input, props.style]} selectionColor="orange" ref={ref} />
            {props.validate ?
                <Span style={{ color: validationColor }}>{props.errors}</Span>
                : null}
        </View>
    )
})