import React, { useEffect, useLayoutEffect, useState } from 'react'
import { Dimensions, ScrollView, Text, View } from 'react-native'
//Navigation
import { useNavigation } from '@react-navigation/core'
import { ParamListBase } from '@react-navigation/native'
import { StackNavigationProp } from '@react-navigation/stack'
//Redux
import { useSelector } from 'react-redux';
import { selectUser } from '../../store/taskReducer';
//Components
import Loading from '../../components/Loading';
import Form from '../../components/Form'
import Comment from './Comment/Comment'
import Button from '../../components/Button'
import FrenzyComponent from './FrenzyComponent'
import * as Yup from "yup"
//Firebase
import { firebaseApp, Firestore } from '../../firebase/firebase';
//Context
import * as FrenzyContext from './FrenzyContext'

const Frenzy = ({ route }: any) => {
    const navigation = useNavigation<StackNavigationProp<ParamListBase, 'Frenzy'>>();
    const user = useSelector(selectUser)
    const [frenzy, setFrenzy] = useState<FrenzyContext.default>({ ...JSON.parse(route.params.frenzy), status: "idle" })
    const [commenting, setCommenting] = useState(false)
    const addComment = async (comment: string) => {
        setCommenting(true)
        try {
            await firebaseApp.firestore().collection("frenzies").doc(frenzy.id).update({
                commentCount: Firestore.FieldValue.increment(1)
            })
            await firebaseApp.firestore().collection("frenzies").doc(frenzy.id).collection("comments").add({
                createdAt: Firestore.FieldValue.serverTimestamp(),
                updatedAt: Firestore.FieldValue.serverTimestamp(),
                comment,
                by: user?.username
            })
        }
        catch (e) {
            alert("Could not add your comment")
        }
        setCommenting(false)
    }
    useLayoutEffect(() => {
        if (frenzy)
            navigation.setOptions({
                headerTitle: `@${frenzy?.user} | ${frenzy?.link}`
            })
    }, [frenzy])
    useEffect(() => {
        async function fetch() {
            firebaseApp.firestore().collection("frenzies").doc(frenzy.id).onSnapshot(async (doc) => {
                let data: any = {
                    ...doc.data(),
                    id: doc.id,
                }
                //Fetch Liked or not
                const liked = await firebaseApp.firestore().collection("frenzies").doc(doc.id).collection("likes").doc(user?.uid).get();
                data = { ...data, liked: liked.exists }
                //Fetch viewed or not
                const viewed = await firebaseApp.firestore().collection("frenzies").doc(doc.id).collection("views").doc(user?.uid).get();
                data = { ...data, viewed: viewed.exists }
                //Fetch comments
                const commentList = await firebaseApp.firestore().collection("frenzies").doc(doc.id).collection("comments").get()
                commentList.docs.map((doc) => doc.data(), doc.id)
                data = {
                    ...data, comments: commentList.docs.map(doc => {
                        return { ...doc.data(), id: doc.id }
                    })
                }
                //Finally making the frenzy loaded
                data = { ...data, status: "loaded" }
                console.log("Changing", data)
                setFrenzy(data as FrenzyContext.default)
            })
        }
        fetch()
    }, [])
    if (frenzy.status === "idle") return <Loading message="Loading Frenzy" />
    return (
        <>
            <ScrollView style={{ flex: 1 }} contentContainerStyle={{ alignItems: "center" }}>
                <FrenzyComponent containerStyle={{ marginVertical: 0, borderRadius: 0, minWidth: Dimensions.get("window").width }} Frenzy={frenzy} navigation={navigation} afterDelete={() => navigation.goBack()} />
                {commenting ? <Loading message="Adding Comment..." /> : (
                    <>
                        <View style={{ minWidth: Dimensions.get("window").width, width: "60%", marginLeft: "auto", marginRight: "auto" }}>
                            {user?.uid ?
                                <>
                                    <Form inputContainerStyle={{ flex: 1, alignItems: "center" }} inputStyle={{ alignSelf: "stretch", width: "auto", borderRadius: 0 }} containerStyle={{ flexDirection: "row", alignItems: "flex-end" }} state={{ comment: "" }} children={[
                                        {
                                            name: "comment",
                                            placeholder: "Write what you feel..."
                                        }
                                    ]} button={{ title: "Comment", style: { marginTop: 0, borderRadius: 0, paddingVertical: 14, width: "auto" } }} onSubmit={(values) => addComment(values.comment)} isComponent={false} />
                                </>
                                : (
                                    <>
                                        <Button style={{ marginLeft: "auto", marginRight: "auto" }} onPress={() => navigation.navigate("Login")}>Login to comment</Button>
                                    </>
                                )}
                        </View>
                        <View style={{ flex: 1 }}>
                            <View style={{ flex: 0.75 }}>
                                {frenzy.comments?.map(comment => (
                                    <Comment Comment={comment} frenzyId={frenzy.id} key={comment.id} />
                                ))}
                            </View>
                        </View>
                    </>
                )}
            </ScrollView>
        </>
    )
}

export default Frenzy

